<template>
	<div class="nav-fixed-custom d-flex flex-row flex-sm-column justify-center align-center">
        <v-btn
            v-for="(item, i) in items"
            :key="i"
            fab
            outlined
            light
            color="white"
            elevation="0"
            class="ma-3"
            @click="$vuetify.goTo(item.link, options)">
            <!-- <v-icon dark>
                mdi-plus
            </v-icon> -->
            {{ item.title }}
        </v-btn>
	</div>
</template>

<style scoped>
    .nav-fixed-custom {
        position: fixed;
        right: 0;
        background-color: transparent;
        height: 100%;
        /*border: 1px solid crimson;*/
    }
    @media only screen and (max-width: 600px) {
        .nav-fixed-custom {
            background-color: black;
            bottom: 0;
            width: 100%;
            height: auto;
            z-index: 10;
        }
    }
    @media only screen and (min-width: 601px) and (max-width: 960px){
        .nav-fixed-custom {
            background-color: transparent;
        }
    }
</style>

<script>
    export default {
		name: 'NavigationBar',

		computed: {
			currentRouteName() {
				return this.$route.name
			},
            options () {
                return {
                    duration: this.duration,
                    offset: this.offset,
                    easing: this.easing,
                }
            },
		},

		created () {
            // 
		},

		props: {
			source: String,
		},

		components: {
            // 
		},

        data: () => ({
			fab: false,
			transition: 'slide-x-reverse-transition',
			drawer: false,
			items: [
				{ title: 'one', icon: 'mdi-folder-open', link: '#onepage-one', color:'black'},
                { title: 'two', icon: 'mdi-folder-open', link: '#onepage-two', color:'black'},
                { title: 'three', icon: 'mdi-folder-open', link: '#onepage-three', color:'black'},
                { title: 'four', icon: 'mdi-folder-open', link: '#onepage-four', color:'black'},
                // { title: 'five', icon: 'mdi-folder-open', link: '#onepage-five', color:'black'},
			],
			route_now: '',
			colorFont: '#0161b8',
            duration: 1000,
            offset: 0,
            easing: 'easeInOutCubic',
        }),
    }
</script>